                                                                            </svg>
                                                                        </span>
                                                                    </div>
                                                                    
                                                                   
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class=" modal-footer">
                                                            <button type="submit" class="btn btn-primary">Upload</button>
                                                        </div> 

                                                        <div v-if="currentFile" class="progress">
                                                                <div
                                                                    class="progress-bar progress-bar-info progress-bar-striped"
                                                                    role="progressbar"
                                                                    :aria-valuenow="progress"
                                                                    aria-valuemin="0"
                                                                    aria-valuemax="100"
                                                                    :style="{ width: progress + '%' }"
                                                                >
                                                                    {{ progress }}%
                                                                </div>
                                                        </div>
                                                    </div>
                                                </div>

                                            </div>
                                        </form>

                                       
                                    </div>
                                </div>

                            </div>

                            <!---section 3 end-->
</template>

<script>
import axios from 'axios'
import { useCookies } from 'vue3-cookies'
export default{

    name:'UploadFile',
    inject : ['hostname'],
    data(){
        return{
            list_file : [],
            display_modal:'display: none;',
            show_modal: '',
            selectedFiles: undefined,
            currentFile: true,
            progress: 0,
            message: "",

            fileInfos: []
        }
    },

    methods:{
       async upload(event){
        this.progress = 0
                this.list_file = Array()
           this.show_modal = 'show'
            this.display_modal = 'display:block;'

            for(var i=0 ; i< event.target.files.length ; i++){
             
                this.list_file.push(event.target.files[i])
            }
          
            
         
           
            },
        formatFileSize(bytes,decimalPoint) {
                if(bytes == 0) return '0 Bytes';
                var k = 1000,
                    dm = decimalPoint || 2,
                    sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
                    i = Math.floor(Math.log(bytes) / Math.log(k));
                return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
                },

        

     async   axiosUpload(){
           const tmp = this
            const formdate = new FormData()
            for(var i =0 ; i < this.list_file.length ; i++){
                formdate.append('file' , this.list_file[i])
            }
            
            
            formdate.append('id_user' , useCookies().cookies.get('info')._id)
            formdate.append('id_folder' , 0)
           await axios.post(this.hostname + '/upload_file' , formdate , {
            onUploadProgress : e => this.progress = Math.round(e.loaded * 100 / e.total)
           }).then( ()=>{
                console.log('uploaded')
                document.getElementById('modal_close_upload').click()
                tmp.$emit('refresh_resent_upload')
           
            }).catch( error => console.log(error))
        }
    },
    
}
</script>

<style scoped>


/* section 3 */

.UploadFile .b_dash{
    
    position: relative;
    
}

#input_file{
   
  position: absolute;
  margin: 0px;
  padding: 0;
  width: 90%;
  height: 90%;
 
  opacity: 0;
    cursor: pointer;

}

