/* section 3 */
.UploadFile h4{
    font-size: 15px;
}

.UploadFile div:nth-child(2) span:nth-child(5){
    font-size: 10px;
}
.UploadFile .b_dash{
    border: 1px dashed black;
    border-radius: 20px;
}
/* section 3 end*/
}
</style>